.ip-modal {
  .ant-modal-content {
    padding: 0 30px 40px;
    text-align: left;
  }
  .ant-modal-close {
    position: absolute;
    right: 16px;
    top: 16px;
  }
  img {
    position: relative;
    margin: -40px auto 0;
    display: block;
    width: 109px;
    height: 100px;
  }
  .access-img-wrap {
    &::before {
      content: '';
      display: table;
    }
  }
  .access-modal-list {
    p {
      font-size: 16px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: #333333;
      line-height: 25px;
      margin: 30px 0 0;
      text-align: left;
    }
  }
}

@media screen and (max-width: 1080px) {
  .ip-modal {
    .ant-modal-content {
      padding: 0 20px 40px;
    }
    .access-modal-list {
      p {
        font-size: 14px;
        line-height: 20px;
      }
    }
  }
}
